import Vue from 'vue'
import Router from 'vue-router'
import user from '@/pages/user'
import driver from '@/pages/driver'
import platform from '@/pages/platform'

Vue.use(Router)

export default new Router({
	routes: [{
		path: '/',
		name: 'login',
		component: user.login
	},
	{
		path: '/home',
		name: 'home',
		component: user.home
	},
	{
		path: '/wait',
		name: 'waitOrder',
		component: user.wait
	},
	{
		path: '/trip',
		name: 'trip',
		component: user.trip
	},
	{
		path: '/trip-detail',
		name: 'trip-detail',
		component: user.trip_detail
	},
	//司机
	{
		path: '/driver',
		name: 'trip-detail',
		component: driver.login
	},
	{
		path: '/driver_home',
		name: 'trip-detail',
		component: driver.home
	},
	{
		path: '/driver_detail',
		name: 'trip-detail',
		component: driver.trip_detail
	},
	{
		path: '/driver_wallet',
		name: 'driver_wallet',
		component: driver.wallet
	},
	{
		path: '/driver_wallet_detail',
		name: 'trip-driver_wallet',
		component: driver.wallet_detail
	},
	{
		path: '/platform',
		name: 'trip-driver_wallet',
		component: platform.login
	},
	{
		path: '/platform_home',
		name: 'platform_home',
		component: platform.home
	}
	]
})



