import selectAddr from '@/components/selectAddr.vue'
import waitingOrder from '@/components/waitingOrder.vue'
import {
	Toast
} from 'vant';
export default {
	data() {
		return {
			attr: {
				show: false,
			},
			waiting_data: {
				show: false
			},
			currentDate1: '',
			showDate: '2007-04-01 03:02:00',
			num: 0,
			popupShow1: false
		}
	},
	components: {
		selectAddr,
		waitingOrder
	},
	computed: {
	},
	filters: {

	},
	methods: {

		select() {
			this.attr.show = true;
		},
		getData() {
			this.attr.show = false;
		},
		getState() {
			this.waiting_data.show = false;
		},
		callCar() {
			this.waiting_data.show = true;
		},
		to_trip() {
			this.$router.push('/trip');
		},
		go() {
			this.$router.push('/wait');
		},
		minus() {
			this.num > 0 ? this.num-- : '';

		},
		changeTime(time) {
			var format = 'yyyy-MM-dd HH:mm:ss';
			var t = new Date(time);
			var tf = function (i) {
				return (i < 10 ? '0' : '') + i
			};
			return format.replace(/yyyy|MM|dd|HH|mm|ss/g, function (a) {
				switch (a) {
					case 'yyyy':
						return tf(t.getFullYear());
						break;
					case 'MM':
						return tf(t.getMonth() + 1);
						break;
					case 'mm':
						return tf(t.getMinutes());
						break;
					case 'dd':
						return tf(t.getDate());
						break;
					case 'HH':
						return tf(t.getHours());
						break;
					case 'ss':
						return tf(t.getSeconds());
						break;
				}
			});

		},
		handlePickerChange() {
			
		},
		handleConfirm() {
			this.popupShow1 = !this.popupShow1;
			this.showDate = this.changeTime(this.currentDate1);
		},
		add() {
			if (this.num < 8) {
				this.num++
			} else {
				Toast('不能超过 8 人');
			}
		}
	}
}



