import { Dialog } from 'vant';
import { Toast } from 'vant';
export default {
    data() {
        return {
            attr: {
                show: false,
            },
            waiting_data: {
                show: false
            },
            is_open: true,
        }
    },
    methods: {
        sendSingle(){
            this.is_open = !this.is_open
        },
        confirm() {
            Dialog.confirm({
                title: '提示',
                message: '您确认将此订单分配给 56 号车吗？'
              }).then(() => {
                // on confirm
                this.is_open = !this.is_open
              }).catch(() => {
                // on cancel
                this.is_open = !this.is_open
              });
        },
        copy(){
            Toast('派车信息已经复制到剪切板！');
        }
    }
}


