import selectAddr from '@/components/selectAddr.vue'
import waitingOrder from '@/components/waitingOrder.vue'
export default {
    data() {
        return {
            attr: {
                show: false,
            },
            waiting_data: {
                show: false
            }
        }
    },
    components: {
        selectAddr,
        waitingOrder
    },
    methods: {
        select(){
            this.attr.show = true;
        },
        getData(){
            this.attr.show = false;
        },
        getState(){
            this.waiting_data.show = false;
        },
        callCar(){
            this.waiting_data.show = true;
        },
        detail(){
            this.$router.push('/trip-detail');
        }
    }
}


