<template>
      <div class="order" v-if="data.show" @click="go_order">
        <h5>等待派单...</h5>
        <p><i class="iconfont icon-paidan"></i></p>
        <div class="time">
            <i class="iconfont icon-shijian"></i><span>5:20</span>
        </div>
        <button @click="point">取消订单</button>
      </div>
</template>
<script>
    export default {
        data(){
            return {
                
            }
        },
        props: [ 'data' ],
        methods: {
            point(){
                this.$emit('child-say');
            },
            go_order(){
                this.$emit('go-router');
            }
        }
    }
</script>
<style scoped lang="less">
    .order {
        text-align: center;
        position: fixed;
        left: 0px;
        top: 0px;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, .8);
        h5 {
            font-size: 2.4rem;
            color: white;
            text-align: center;
            margin-top: 25%;
        }
        p {
            text-align: center;
            margin-top: 2rem;
            i {
                font-size: 7rem;
                color: white;
            }
            
        }
        .time {
            color: white;
            text-align: center;
            margin-top: 1rem;
            font-size: 2rem;
            i {
                font-size: 2rem;
            }
            span {
                margin-left: .5rem;
            }
            
            
        }
        button {
            width: 11rem;
            line-height: 3rem;
            border: none;
            background: #393A3F;
            border-radius: 1.5rem;
            color: #C3C3C3;
            position: absolute;
            left: 50%;
            bottom: 20%;
            margin-left: -5.5rem;
        }
    }
</style>



