import { Dialog } from 'vant';
export default {
    data(){
        return {

        }
    },
    methods: {
        callDriver() {
            window.location.href = "tel:10086";
        },
        canleOrder(){
                Dialog.confirm({
                  title: '提示',
                  message: '您确定要取消该订单吗？'
                }).then(() => {
                  // on confirm
                  this.$router.push('./home')
                }).catch(() => {
                  // on cancel
                });
              
        }
    }

}


